<template>
  <div class="line">
    <div class="progress" :style="{
				width: progression + '%',
				background: backgroundColor,
				color: length % 10 ? '#000' : '#fff'
			}">{{length}}</div>
  </div>
</template>

<script>
export default {
  name: "Counter",
  props: ['progression', 'length'],
  data(){
    return{

    }
  },
  computed:{
    backgroundColor(){
      return '#cfef' + this.length % 10  + this.length % 10;
    }
  }
}
</script>

<style scoped>
  .line {
    border: 2px solid #ccc;
    height: 20px;
    border-radius: 3px;
    margin-bottom: 10px;
  }
  .progress{
    width: 50%;
    height: 100%;
    transition: 1s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>